<template>
  <div class="jbrlogin-main">
    <!-- <div class="jbrlogin-h" /> -->
    <div class="login-h">账号密码登录</div>
    <router-link to="/GysZcXzjs">
      <div class="login-h-mini">还没有账号，立即<a>注册</a></div>
    </router-link>
    <div class="login-form">
      <van-cell-group>
        <div class="input-item">
          <van-field
            ref="name"
            v-model="users.userAccount"
            v-validate="{'required': 'true'}"
            :error-message="errors.first('userAccount')"
            clearable
            name="userAccount"
            placeholder="请输登录账号"
          />
        </div>

        <div class="input-item password">
          <van-field
            ref="id"
            v-model="users.passWord"
            v-validate="{'required': 'true'}"
            clearable
            :error-message="errors.first('password')"
            :type="eyesChange ==true?'text':'password'"
            name="password"
            placeholder="请输入登录密码"
          />

          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#iconkejian" />
            </svg>
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#iconbukejian" />
            </svg>
          </div>
        </div>
      </van-cell-group>
      <!-- 登录按钮 begin -->
      <div class="login-button">
        <van-button round type="info" @click="handleSubmit">登 录</van-button>
      </div>
      <!-- 登录按钮 end -->

      <!-- 登录按钮 end -->
      <!-- <div class="login-txt">
        <van-button type="default" @click="">账号演示设置</van-button>
      </div> -->
    </div>
  </div>

</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'

import { getUserName, getPass } from '../../../common/js/catch'
export default {
  title: 'login',
  components: {
  },
  mixins: [NativeJS],
  data() {
    return {
      profiletitle: '',
      /* 未登录弹出框*/
      status: {
        isShowPublish: false,
        topNum: 20
      },
      isEmpty: true, // 判断按钮是否可以点击
      loading: false,
      eyesChange: false, // 是否展示密码

      users: {
        userAccount: getUserName(),
        passWord: getPass()
      },

      captchaCodeImg: null, // 验证码地址
      codeNumber: null, // 验证码
      showAlert: false, // 显示提示组件
      alertText: null // 提示的内容
    }
  },
  // body定义不同背景色
  beforeCreate: function() {
    this.$router.qu
    document.getElementsByTagName('body')[0].className = 'body-bg'
  },
  beforeDestroy: function() {
    document.body.removeAttribute('class', 'add_bg')
  },
  created() {
    this.users.userAccount = getUserName()
    this.users.passWord = getPass()
    if (this.users.userAccount != null && this.users.passWord != null) {
      this.isEmpty = false
    }
    // this.getFiles()
  },
  methods: {
    checkData() {
      if (this.users.userAccount && this.users.passWord) {
        this.isEmpty = false
      } else {
        this.isEmpty = true
      }
    },
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    // handleZC() {
    //   this.$router.push('/gyszc')
    // },
    handleSubmit() {
      this.$validator.validate().then(result => {
        console.log('result------------------------', result)
        if (result) {
          this.loading = true
          this.$store
            .dispatch('user/login', this.users)
            .then(() => {
              // 调整转商城首页
              this.$router.push('/mallhome')
              this.loading = false
            })
            .catch((error) => {
              console.log(error)
              this.loading = false
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}

</script>

<style lang="css" scoped>

</style>
